esired 16px size;\n//      [3]     Prevent adjustments of font size after orientation changes in iOS.\n//  ----------------------------------------------------------------------------\n\nhtml,\nbody {\n    margin: 0;\n    /* stylelint-disable-next-line meowtec/no-px */\n    font-size: 10px; // [1]\n}\n\nbody {\n    color: var(--base-color-text);\n    font-size: var(--base--font-size); // [2]\n    font-family: var(--base--font-family);\n    line-height: var(--base--line-height);\n    background-color: var(--base-color-background);\n    -webkit-text-size-adjust: 100%; // [3]\n}\n\n\n//  ============================================================================\n//  @   UNIVERSAL SVG STYLES\n//  ----------------------------------------------------------------------------\n.d-svg {\n    //  By default, most icons are ~24px.\n    width: 1.5em;\n    height: 1.5em;\n\n    //  All system SVGs should inherit the current text color\n    &.d-svg--system {\n        fill: currentColor;\n    }\n}\n\n.d-svg-primary--stroke {\n    stroke: var(--primary-color);\n}\n\n.d-svg-primary--fill {\n    fill: var(--primary-color);\n}\n\n//  ============================================================================\n//  @   FOCUS VISIBLE\n//  ----------------------------------------------------------------------------\n// This will hide the focus indicator if the element receives focus via the mouse,\n// but it will still show up on keyboard focus.\n//\n.js-focus-visible :focus:not(.focus-visible) {\n  outline: none;\n}\n","//@import (reference) '../variables/colors.less';\n//@import (reference) '../variables/layout.less';\n//@import (reference) '../variables/borders.less';\n//@import (reference) '../variables/visual-styles.less';\n//@import (reference) '../variables/icons.less';\n//@import (reference) '../variables/sizes.less';\n//@import (reference) '../variables/typography.less';\n//@import (reference) '../utilities/internals.less';\n//@import (reference) '../utilities/grid.less';\n\n//\n//  DIALTONE\n//  THEME VARIABLES\n//\n//  This file contains all the theme-able portions of Dialtone.\n//  For further information about these classes, please visit:\n//  https://dialpad.design/themes\n//\n//  ============================================================================\n//  THEME VARIABLES\n//  The following items are themeable items that will update the imported\n//  Dialtone styles and components accordingly.\n//\n//  TABLE OF CONTENTS:\n//    - SET VARIABLES\n//      - Colors\n//      - Visual Styles\n//      - Theme\n//    - OUTPUT VARIABLES\n//\n//  ============================================================================\n//  $   SET VARIABLES\n//  ----------------------------------------------------------------------------\n\n\n@theme-vars: {\n    primary-color-h:                        var(--purple-400-h);\n    primary-color-s:                        var(--purple-400-s);\n    primary-color-l:                        var(--purple-400-l);\n    primary-color-hsl:                      var(--primary-color-h) var(--primary-color-s) var(--primary-color-l);\n    primary-color:                          hsl(var(--primary-color-h) var(--primary-color-s) var(--primary-color-l));\n    primary-color-hover:                    hsl(var(--primary-color-h) var(--primary-color-s) calc(var(--primary-color-l) - 10%));\n    nav-background-color-h:                 var(--purple-600-h);\n    nav-background-color-s:                 var(--purple-600-s);\n    nav-background-color-l:                 var(--purple-600-l);\n    nav-background-color:                   hsl(var(--nav-background-color-h) var(--nav-background-color-s) var(--nav-background-color-l));\n\n    topbar-height:                                    var(--size-700);\n\n    base--font-size:                                  var(--fs-200);\n    base--font-family:                                @ff-custom;\n    base--line-height:                                var(--lh-300);\n    base--corner-radius:    